import "core-js/modules/es.array.push.js";
import { reactive, ref, toRefs } from "@vue/reactivity";
import { getShopMessageData2 } from "../assets/api/home";
import router from "@/router";
import store from "@/store";
// import { useRoute } from 'vue-router';
export default {
  setup() {
    // let route = useRoute();
    //总价格
    let money = JSON.parse(window.localStorage.getItem("allmoney"));

    //页面跳转支付
    let pay = () => {
      router.push({
        path: "/pay"
      });
    };
    //返回上一页
    let back = () => {
      console.log("1");
      router.push({
        path: "/shoppingcar"
      });
    };
    //一个书本的详情
    let bannerDataFun = reactive({
      bannerData: []
    });
    //举个例子引入的信息
    let getShopMessageDataFun = () => {
      getShopMessageData2().then(data => {
        console.log(data);
        //轮播图信息
        bannerDataFun.bannerData = data.data.results.body.detail;
      });
    };

    //选择地址跳转
    let changeaddress = () => {
      router.push({
        path: "/addreslocal"
      });
    };

    //读取存入本地的点击
    let num = JSON.parse(window.localStorage.getItem("num"));
    //地址信息
    let message = JSON.parse(window.localStorage.getItem("address"));

    //判断是否显示
    let haveshow = ref("true");
    let localmessage = ref("");

    //没有值或者长度为0
    if (!message || message.lenth == 0) {
      haveshow.value = true;
      // 否则显示
    } else {
      //查找下标
      let index = message.findIndex(item => {
        return item.id == num;
      });
      if (index == -1) {
        haveshow.value = true;
      } else {
        haveshow.value = false;
        // 找到的话就显示
        localmessage.value = message[index];
      }
    }

    //新建一个空数组来接受请求数据
    let todata = reactive({
      buyData: []
    });
    let gettoday = () => {
      todata.buyData = store.state.readyCar;
      console.log(todata.buyData[0].bannerData);
    };
    gettoday();
    getShopMessageDataFun();
    return {
      ...toRefs(bannerDataFun),
      ...toRefs(todata),
      pay,
      back,
      money,
      changeaddress,
      localmessage,
      message,
      haveshow
    };
  }
};